@mixin mq($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.rowC {
  display: flex;
  padding-left: 10%;
  width: 80%;
  height: 100%;
  @include mq(900px) {
    flex-direction: column;
  }
}
.label {
  padding: 20px;
  width: 100%;
  height: 100%;
}
.spinner {
  position: relative;
  top: 10%;
  left: 50%;
}
